/* row with flex class for header items  */
.experience{
    /* background-color: #e3f2fd; */
    padding: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.row {
    display: flex;
  }
  .box {
    padding: 0;
    margin: 0;
  }
  
  .containerExp {
    width: 400px;
    height: 200px;
    /* border: 1px solid red; */
    box-shadow: 0 15px 25px rgba(0,0,0,0.5);
  }
  
  .boxMiddle {
    width: calc(100% - 121px) !important;
    background-size: cover;
    padding-left: 20px;
  }
  
  .LM {
    width: 1px;
    border: 1px solid blue;
    height: calc(100% - 30px) !important;
    left: 0px;
  }
  
  .RM {
    width: 1px;
    border: 1px solid blue;
    height: calc(100% - 30px) !important;
    right: 0px;
    position: relative;
  }
  
  .bottom {
    width: 100%;
    height: 0px;
    border: 1px solid blue;
  }
  
  .content {
    background: #ababab;
  
    /*   padding for content area */
    padding: 10px;
  
    /*   height minus the padding */
    height: 150px;
  }
  
  .TL {
    width: 25px;
    height: 29px;
    background: url("data:image/png;base64,");
  }
  
  .TM {
    width: 200px;
    height: 29px;
    line-height: 26px;
    background: url("data:image/png;base64,");
  }
  
  .TR {
    width: 75px;
    height: 29px;
    background: url("data:image/png;base64,");
  }